import UIBase from "../UIBase";
import UIHelp from "../../../logic/ui/UIHelp";
import GameDataCenter from "../../../data/GameDataCenter";
import EventMng from "../../../manager/EventMng";
import { SocketEvent, GameEvent,Constant } from "../../../data/const/EventConst";
import { Log, LOG_TAG } from "../../../utils/Log";
import auto_zhujiemianUI from "../../../data/autoui/scene/auto_zhujiemianUI";
import Utils from "../../../utils/Utils"

const { ccclass, menu, property } = cc._decorator;

enum MapEnum {
    hei,
    hong,
    lan,
    lv,
    zi
  }

@ccclass
@menu("UI/scene/UIBattle")
export default class UIBattle extends UIBase {
    @property([cc.Node])
    public mMap: cc.Node[] = [];
    @property([cc.Node])
    public mThrough: cc.Node = null;
    @property([cc.Node])
    public mWormhole: cc.Node = null;
    CountDownLabel = null;
    HoursLabel = null;
    MinutesLabel = null;
    SecondsLabel = null;
    CountTime = 0;

    protected mCreateWormhole:number = Constant.CREATEWORMHOLETIME;
    protected mEnterWormhole:number = Constant.ENTERWORMHOLETIME;
    protected mIsEnterWormhole:boolean = true;
    UFOSize:cc.Vec2 = new cc.Vec2(Constant.UFOWIDTH,Constant.UFOHEIGHT);
    map_spine:sp.Skeleton;
    through_spine:sp.Skeleton;
    randomMap:number = 0;
    UFPPos = cc.Vec2.ZERO;


    ui: auto_zhujiemianUI = null;

	protected static prefabUrl = "db://a";
	protected static className = "UIBattle";

	onUILoad() {
         this.ui = this.node.addComponent(auto_zhujiemianUI);
         this.CountDownLabel = this.ui.CountDown.getComponent(cc.Label);
         this.HoursLabel = this.ui.Hours.getComponent(cc.Label);
         this.MinutesLabel = this.ui.Minutes.getComponent(cc.Label);
         this.SecondsLabel = this.ui.Seconds.getComponent(cc.Label);
         cc.game.on(cc.game.EVENT_HIDE, function(){
            GameDataCenter.battle.GameOver("",0);
            },this);

        this.schedule(function() {
                this.CountDown();
            }, 1);

        this.map_spine = this.mWormhole.getComponent('sp.Skeleton')
        this.through_spine = this.mThrough.getComponent('sp.Skeleton')
        // this.through_spine.setEndListener(function() {
        //     console.log("anim end=============");
        //     this.mThrough.active = false;
        // });
            
        // this.through_spine.setCompleteListener(this.SetThroughActive);
        this.ui.ZJMUI_1_3.active = false;
	}

	onRegisterEvent() {
		// this.addEventListener(this.ui.btn_notice, this.onOpenNotice, this);
		EventMng.on(GameEvent.ENTERWORMHOLE, this.EnterWormhole, this);
    }

	unRegisterEvent() {
        // this.unRegisterEvent(this.ui.btn_notice, this.onOpenNotice, this);
        EventMng.off(GameEvent.ENTERWORMHOLE, this.EnterWormhole, this);
    }
    

	onStart() {
        this.randomMap = Math.floor(Math.random()*5);
        this.SetMapActive(this.randomMap);
	}
    
    onDestroy()
    {
        this.unschedule(this.CountDown);
    }

    CountDown()
    {
        this.CountTime++;
        let tiemArr = Utils.GetHMS(this.CountTime);
        this.HoursLabel.string = tiemArr[0];
        this.MinutesLabel.string = tiemArr[1];
        this.SecondsLabel.string = tiemArr[2];
        console.log("==================",this.mCreateWormhole)

        if (this.mCreateWormhole > 0)
        {
            this.mCreateWormhole -= 1;
        }
        else
        {
            this.mIsEnterWormhole = false;
            this.mEnterWormhole = Constant.ENTERWORMHOLETIME;
            this.CreateWormhole();
            this.ui.ZJMUI_1_3.active = true;
        }

        if (!this.mIsEnterWormhole)
        {

            if(this.mEnterWormhole > 0)
            {
                this.mEnterWormhole -=1;
            }
            else
            {
                GameDataCenter.battle.GameOver("",0);
                this.unschedule(this.CountDown);
            }
            this.CountDownLabel.string = this.mEnterWormhole.toString()
        }
        else
        {
            this.ui.ZJMUI_1_3.active = false;
        }
       
    } 

    CreateWormhole()
    {
        this.mCreateWormhole = Constant.CREATEWORMHOLETIME;
        this.randomMap = Math.floor(Math.random()*5);
        this.UFPPos = this.GeneratePos();

        let rect = new cc.Rect(this.UFPPos.x,this.UFPPos.y,Constant.UFORADIUS,Constant.UFORADIUS);
        this.mWormhole.active = true;
        Log.log(LOG_TAG.debug,"x",rect.x)
        Log.log(LOG_TAG.debug,"y",rect.y)

        this.map_spine.setAnimation(0, MapEnum[this.randomMap], true);
        this.mWormhole.setPosition(this.UFPPos);
        EventMng.emit(GameEvent.CREATEWORMHOLE,rect);
    }

    EnterWormhole()
    {
        Log.log(LOG_TAG.debug,"enter==========");
        this.mThrough.active = true;
        this.mThrough.setPosition(this.UFPPos);
       
        this.scheduleOnce(this.SetThroughActive, 0.5);
        this.mIsEnterWormhole = true;
        
        this.mWormhole.active = false;
        this.SetMapActive(this.randomMap)
    }

    GeneratePos()
    {
        let randomX = Math.floor(Math.random()*320);
        let randomY = Math.floor(Math.random()*650);
        let randomX_plus = Math.floor(Math.random());
        let randomY_plus = Math.floor(Math.random());
        if (randomX_plus < 0.5)
        {
            randomX = -randomX;
        }
        if (randomY_plus < 0.5)
        {
            randomY = -randomY;
        }
        let nPos = new cc.Vec2(randomX,randomY);

        let minX = -this.node.width/2 + this.UFOSize.x/2; //X
        let maxX = Math.abs(minX);
        let minY = -this.node.height/2 + this.UFOSize.y/2; //Y
        let maxY = Math.abs(minY);
 
        if (nPos.x < minX) {
            nPos.x = minX;
        };
        if (nPos.x > maxX) {
            nPos.x = maxX;
        };
        if (nPos.y < minY) {
            nPos.y = minY;
        };
        if (nPos.y > maxY) {
            nPos.y = maxY;
        };
        return nPos;
    }


    SetMapActive (random) {
        for (let index = 0; index < 5; index++) {
            if (index == random)
            {
                this.mMap[index].active = true;
            }
            else
            {
                this.mMap[index].active = false;
            }
            
        }
    }

    SetThroughActive () {
        this.mThrough.active = false;
        this.unschedule(this.SetThroughActive);
    }
}
