import UIBase from "../UIBase";
import auto_victory from "../../../data/autoui/scene/auto_victory";

const { ccclass, menu, property } = cc._decorator;

@ccclass
@menu("UI/scene/UIVictory")
export default class UIVictory extends UIBase {
	ui: auto_victory = null;

	protected static prefabUrl = "db://a";
	protected static className = "UIVictory";

	onUILoad() {
		// this.ui = this.node.addComponent(auto_victory);
	}

	onRegisterEvent() {
        this.addEventListener(this.node, this.onClose, this);
	}

	unRegisterEvent() {
        this.removeEventListener(this.node, this.onClose, this);
	}

    onClose()
    {
        cc.director.loadScene('denglu');
    }
    
}