const KEY_ACCOUNT = 'battle';

export default class BattleModel extends IDataModel {
    mIsOver = false;
    constructor() {
        super('battle');
    }

    /** */
    getMessageListeners() {
        return {
            // keyvalue
            // ['G2C_Login']: (msg) => { this.G2C_LoginSuccess(msg) },
        }
    }

    
    public GameOver(reason: string,result:number) {
        if (this.mIsOver)
        {
            return;
        }
        console.log("=======================game over")
        this.mIsOver = true;
        if (result > 0)
        {
            cc.director.loadScene('victory')
        }
        else
        {
            cc.director.loadScene('regret')
        }
        
        this.mIsOver = false;
        // UIHelp.ShowDialog({
        //     title: '', content: reason,
        //     certainCb: () => {
        //         cc.director.loadScene('denglu')
        //     }
        // })
    }


}