import { SocketEvent } from "../data/const/EventConst";
import { MessageRegister } from "./Message";
import EventMng from "../manager/EventMng";
import { ISocket, SocketState, WbSocket, WxSocket } from "./Socket";
import { Log, LOG_TAG } from "../utils/Log";
import UIHelp from "../logic/ui/UIHelp";
import GameController from "../GameController";
import {msg }from "../../resources/proto/proto";

const DATA_TOTAL_LEN = 4;	//
const PROTOCOLTYPE_LEN = 2;	//

export interface ISocketDelegate {
    onSocketOpen();
    onSocketMessage(data: string | ArrayBuffer);
    onSocketError(errMsg);
    onSocketClosed(msg: string);
}

/**
 * socket
 */
export class SocketDelegate implements ISocketDelegate {
    private _socket: ISocket;

    isSocketOpened() {
        return (this._socket && this._socket.getState() == SocketState.OPEN);
    }

    isSocketClosed() {
        return this._socket == null;
    }

    connect(url: string) {
        Log.log(LOG_TAG.SOCKET, 'connect socket = ' + url);
        // socket
        if (cc.sys.platform === cc.sys.WECHAT_GAME) {
            this._socket = new WxSocket(url, this);
        } else {
            this._socket = new WbSocket(url, this);
        }
        this._socket.connect();

    }

    closeConnect() {
        if (this._socket) {
            this._socket.close();
        }
    }

    onSocketOpen() {
        Log.log(LOG_TAG.SOCKET, 'socket open');
        EventMng.emit(SocketEvent.SOCKET_OPEN);
    }

    onSocketError(errMsg) {
        errMsg && Log.error('socket error, msg = ' + errMsg);
        // democonnect
        UIHelp.ShowDialog({
            title: '', content: '',
            certainCb: () => {
                GameController.network.connect();
            }
        })
    }

    onSocketClosed(msg: string) {
        Log.log(LOG_TAG.SOCKET, 'socket close, reason = ' + msg);
        if (this._socket) {
            this._socket.close();
        }
        this._socket = null;
        EventMng.emit(SocketEvent.SOCKET_CLOSE);
    }

    onSocketMessage(data: string | ArrayBuffer) {
        if (this.isSocketClosed()) {
            Log.error('onMessage call but socket had closed')
            return;
        }
        let msgdata;
        let protocolID;
        if (typeof (data) === 'string') {
            msgdata = data;
            console.log("data",data);
        } else {
            let msgArr = this.bufferToMsg(data);
            msgdata = msgArr[0];
            protocolID = msgArr[1];
            console.log("msg",msgdata);
            
        }
        Log.log(LOG_TAG.SOCKET, 'recieve msg = ', msgdata);
        EventMng.emit(msg.eMsgID[protocolID], msgdata);
    }

    send(msgData,protocolID) {
        if (typeof (msgData) === 'string') {
            this._socket.send(msgData);
        } else {
            let buf = this.msgToBuffer(msgData,protocolID)
            this._socket.send(buf);
        }
    }

    /**
     * buffermsg
     * 4=+ + 2 + 
     * @param recvBuf 
     */
    private bufferToMsg(recvBuf: ArrayBuffer) {
        // let recvView = new DataView(recvBuf);
        // let protocolType = recvView.getInt8(DATA_TOTAL_LEN);
        // let msgBuf = recvBuf.slice(DATA_TOTAL_LEN + PROTOCOLTYPE_LEN, recvBuf.byteLength);
        // let classType = MessageRegister.getClass(protocolType);
        // let msg = new classType(msgBuf);

        let recv_data = recvBuf;
	    let dv = new DataView(recv_data);
        let protocolID = dv.getInt16(0,true) // n 16-bit(2).
        let dattaLen = dv.getInt32(PROTOCOLTYPE_LEN,true)
	   //			
	   let data_body = new Uint8Array(recv_data, DATA_TOTAL_LEN + PROTOCOLTYPE_LEN); 
	   //PB
	//   let message = msg.Login.decode(data_body);//

        return [data_body,protocolID];
    }

    /**
     * msgbuffer
     * 4=+ + 2 + 
     * @param msg 
     */
    private msgToBuffer(msgData,protocolID) {

        let dataLen = msgData.length;

        let sendBuf = new ArrayBuffer(DATA_TOTAL_LEN + PROTOCOLTYPE_LEN + dataLen);
        let sendView = new DataView(sendBuf);
      
        sendView.setInt16(0, protocolID,true);
        sendView.setInt32(PROTOCOLTYPE_LEN, dataLen,true);
        // sendView.setInt16(0, protocolID);
        // sendView.setInt32(PROTOCOLTYPE_LEN, DATA_TOTAL_LEN+PROTOCOLTYPE_LEN + dataLen);
       
        
        var u8view = new Uint8Array(sendBuf, DATA_TOTAL_LEN+PROTOCOLTYPE_LEN);
        for (let i = 0; i < dataLen; ++i) {
            u8view[i] = msgData[i];
        }

        return sendBuf;
    }
}