import GameDataCenter from "../../../data/GameDataCenter";
import UIHelp from "../../../logic/ui/UIHelp";
import EventMng from "../../../manager/EventMng";
import { GameEvent,LanguageKey } from "../../../data/const/EventConst";

const {ccclass, property} = cc._decorator;

@ccclass
export default class PlayerControl extends cc.Component {
    
    nodePos: cc.Vec2 = cc.Vec2.ZERO;
    dir: cc.Vec2 = cc.Vec2.ZERO;
    player_spine:sp.Skeleton;
    rect:cc.Rect = null;

    // update (dt) {
    //     if(this.dir.mag()<0.5){
    //         return;
    //     }

       

    //     // var r = Math.atan2(this.dir.y,this.dir.x);
    //     // var degree = r * 180/(Math.PI);
    //     // degree = 360 - degree + 90;
    //     // this.node.rotation = degree;
    // }

    onLoad () {
        this.player_spine = this.getComponent('sp.Skeleton');
        
        this.node.parent.on(cc.Node.EventType.TOUCH_START, this.onTouchStart, this);
        this.node.parent.on(cc.Node.EventType.TOUCH_MOVE, this.onTouchMove, this);
        this.node.parent.on(cc.Node.EventType.TOUCH_END, this.onTouchEnd, this);
        this.node.parent.on(cc.Node.EventType.TOUCH_CANCEL, this.onTouchCancel, this);
        EventMng.on(GameEvent.CREATEWORMHOLE, this.CreateRect, this);
    }
    
    onDestroy() {
    
        this.node.parent.off(cc.Node.EventType.TOUCH_START, this.onTouchStart, this);
        this.node.parent.off(cc.Node.EventType.TOUCH_MOVE, this.onTouchMove, this);
        this.node.parent.off(cc.Node.EventType.TOUCH_END, this.onTouchEnd, this);
        this.node.parent.off(cc.Node.EventType.TOUCH_CANCEL, this.onTouchCancel, this);
        EventMng.off(GameEvent.CREATEWORMHOLE, this.CreateRect, this);
}

    onTouchStart (event) {
        if (event.getTouches() > 1)
        {
            this.GameOver(LanguageKey.GAMEOVERTIP2)
            return;
        }

        // this.player_spine.setAnimation(0, 'animation', true); 

        let oldPos = event.getLocation();
        oldPos = this.node.parent.convertToNodeSpaceAR(oldPos);
      
        // let len = oldPos.mag();
        // this.dir.x = oldPos.x / len;
        // this.dir.y = oldPos.y / len;
        this.nodePos = oldPos;
        this.node.setPosition(this.nodePos);
        // this.rect = new cc.Rect(200, 200, 30, 30);
    }
 
    //
    onTouchMove (event) {
        if (event.getTouches() > 1)
        {
            this.GameOver(LanguageKey.GAMEOVERTIP2)
            return;
        }
        
        let touches = event.getTouches();
        //
        let oldPos = this.node.parent.convertToNodeSpaceAR(touches[0].getStartLocation());
     
        //
        let newPos = this.node.parent.convertToNodeSpaceAR(touches[0].getLocation());
        // let newPos1 = this.node.convertToNodeSpaceAR(event.getLocation());
        // console.log("=======================x",newPos.x)
        //     console.log("=======================y",newPos.y)
        //     let localPos = touches[0].getLocation();
        //     console.log("=======================local x",localPos.x)
        //     console.log("=======================localPos.y",localPos.y)
        // this.onGetAn(newPos)
        // var len = newPos1.mag();
        // this.dir.x = newPos1.x / len;
        // this.dir.y = newPos1.y / len;
 
        let subPos = oldPos.sub(newPos); // 2.X p1.sub(p2);
 
        this.node.x = this.nodePos.x - subPos.x;
        this.node.y = this.nodePos.y - subPos.y;
        
        // ; 
        let minX = -this.node.parent.width/2 + this.node.width/2; //X
        let maxX = Math.abs(minX);
        let minY = -this.node.parent.height/2 + this.node.height/2; //Y
        let maxY = Math.abs(minY);
        let nPos = this.node.getPosition(); //
 
        if (nPos.x < minX) {
            nPos.x = minX;
        };
        if (nPos.x > maxX) {
            nPos.x = maxX;
        };
        if (nPos.y < minY) {
            nPos.y = minY;
        };
        if (nPos.y > maxY) {
            nPos.y = maxY;
        };
        this.node.setPosition(nPos);
       
        
        // let aa = this.rect.contains(nPos);// true
        // console.log("=======================x",touches[0].getLocation().x)
        // console.log("=======================y",touches[0].getLocation().y)
        
        if (this.rect == null)
        {
            return;
        }
        if (this.rect.contains(nPos))
        {
            EventMng.emit(GameEvent.ENTERWORMHOLE);
            this.rect = null;
            console.log("=======================x",newPos.x)
            console.log("=======================y",newPos.y)
            // console.log("=======================x",touches[0].getLocation().x)
            // console.log("=======================y",touches[0].getLocation().y)
        }

    }
    onGetAn(point){
        var pos = this.node.getPosition();
        let angle = Math.atan2(point.y - pos.y, point.x - pos.x) * (180/Math.PI);
        this.node.angle = angle
    }

    onTouchEnd () {
        this.GameOver(LanguageKey.GAMEOVERTIP1)
    }

    onTouchCancel() {
        this.GameOver(LanguageKey.GAMEOVERTIP1)
    }

    GameOver(tip){
        GameDataCenter.battle.GameOver(tip,0);
       
    }

    CreateRect(rect)
    {
        // this.rect = new cc.Rect(320, 650, 50, 50);
        this.rect = rect;
    }
}
