import { SocketDelegate } from "./SocketDelegate";
import { Log } from "../utils/Log";

export class Network {
    private _socket: SocketDelegate = null;
    // private _url: string = 'ws://10.15.119.111:8100';
    private _url: string = 'ws://10.15.119.111:7001';
    // private _url: string = 'ws://10.15.119.111:7100';

    constructor() {
        // this.safeConnectSocket();
    }

    close() {
        this.safeCloseSocket();
    }

    send(msgData,protocolID) {
        if (!this._socket.isSocketOpened()) {
            Log.error('send message but socket not open!')
            return;
        }
        this._socket.send(msgData,protocolID);
    }

    connect(url=null) {
        this.safeConnectSocket(url);
    }

    GetIsOpen()
    {
        return this._socket.isSocketOpened();
    }

    private safeConnectSocket(url) {
        if (this._socket != null) {
            this._socket.closeConnect();
        }
        if (url != null)
        {
            this._url = url;
        }
        this._socket = new SocketDelegate();
        this._socket.connect(this._url);
    }

    private safeCloseSocket() {
        if (this._socket != null) {
            this._socket.closeConnect();
        }
        this._socket = null;
    }
}