import IDataModel from "./IDataModel";
import UIHelp from "../../logic/ui/UIHelp";
import EventMng from "../../manager/EventMng";
import { GameEvent } from "../const/EventConst";

import {msg }from "../../../resources/proto/proto";
import {Log}from "../../utils/Log";
import GameController from "../../GameController";

const KEY_ACCOUNT = 'account';

export default class AccountModel extends IDataModel {
   
    checkCode = "";
    constructor() {
        super('account');
    }

    /** */
    saveAccount(account: string) {
        this.Set(KEY_ACCOUNT, account);
        this.Save();
    }

    /** */
    getAccount() {
        return this.Query(KEY_ACCOUNT, '');
    }

    /** */
    getMessageListeners() {
        // UIHelp.ShowTips('');
        return {
            
            // keyvalue
            // console.log("msg.eMsgID.eMsg_AccountCheck_Quest)",msg.eMsgID.eMsg_AccountCheck_Quest);
        
            [msg.eMsgID[msg.eMsgID.eMsg_AccountCheck_Resp]]: (data) => { this.AccountCheck_Resp(data) },
            [msg.eMsgID[msg.eMsgID.eMsg_PlayerLogin_Resp]]: (data) => { this.G2C_LoginSuccess(data) },
            [msg.eMsgID[msg.eMsgID.eMsg_LoginFlowFinish_Resp]]: (data) => { this.LoginSuccess(data) },
        }
    }

    /** */
    AccountCheck_Quest(account: number, session_key: string) {
        let login = msg.AccountCheck_Quest.create({iggId:account,sessionKey:session_key});
        let messagebuf = msg.AccountCheck_Quest.encode(login).finish();
        console.log("decode ", JSON.stringify(msg.AccountCheck_Quest.decode(messagebuf)));
        this.sendProtocolMsg(messagebuf,msg.eMsgID.eMsg_AccountCheck_Quest);
    
        
        // this.saveAccount(account);
    }
    AccountCheck_Resp(data) {
        let message = msg.AccountCheck_Resp.decode(data);//
        console.log("======="+message.checkCode);
        this.checkCode = message.checkCode;
        console.log("======="+message.ip);
        console.log("======="+message.port);
        // 
        GameController.network.close();
        let url = "ws://"+message.ip+":"+message.port;
		GameController.network.connect(url);
    }

    /** */
    // Login(account: string, password: string) {
    Login() {
       
        // let req = proto.create({param1: 1, param2: 2});
        // let buffer = HeartBeatReq.encode(req).finish();



        let login = msg.PlayerLogin_Quest.create({iggId:11125661,checkOutCode:this.checkCode});
        let messagebuf = msg.PlayerLogin_Quest.encode(login).finish();
        // console.log("decode ", JSON.stringify(msg.AccountCheck_Quest.decode(messagebuf)));
        this.sendProtocolMsg(messagebuf,msg.eMsgID.eMsg_PlayerLogin_Quest);
        
        // console.log(msg.eMsgID.eEvent_BuyItems.toString())
        // console.log(msg.eMsgID.eEvent_BuyItems)
        
        // this.saveAccount(account);
    }

    /** */
    G2C_LoginSuccess(data) {
        let message = msg.AccountCheck_Resp.decode(data);//
        
        UIHelp.ShowTips('');
        console.log(msg.eMsgID[20])

        EventMng.emit(GameEvent.LOGIN_SUCCESS);
    }
    LoginSuccess(data)
    {
        let message = msg.LoginFlowFinish_Resp.decode(data);//
        console.log("igg",message.iggId);
    }
}