import UIBase from "../UIBase";
import UIHelp from "../../../logic/ui/UIHelp";
import GameDataCenter from "../../../data/GameDataCenter";
import EventMng from "../../../manager/EventMng";
import { SocketEvent, GameEvent,Constant } from "../../../data/const/EventConst";
import auto_wanfa from "../../../data/autoui/popup/auto_wanfa";

const { ccclass, menu, property } = cc._decorator;

@ccclass
@menu("ui/popup/UIRules")
export default class UIRules extends UIBase {

    ui: auto_wanfa = null;

	protected static prefabUrl = "popup/wanfa";
	protected static className = "UIRules";

	onUILoad() {
        this.ui = this.node.addComponent(auto_wanfa);
	}

	onRegisterEvent() {
		this.addEventListener(this.ui.btn_close, this.onClose, this);
		// EventMng.on(GameEvent.ENTERWORMHOLE, this.EnterWormhole, this);
    }

	unRegisterEvent() {
        this.removeEventListener(this.ui.btn_close, this.onClose, this);
        // EventMng.off(GameEvent.ENTERWORMHOLE, this.EnterWormhole, this);
    }
    
    // onDestroy()
    // {
       
    // }
    onClose()
    {
       UIHelp.CloseUI(UIRules);
    }

}
