class Utils {
   
    GetHMS(times)
    {
        let hour = Math.floor(times / 3600);
        let residue = times - hour * 3600;
        let minute = Math.floor(residue / 60);
        let second = residue - minute * 60;
        let sHour = hour.toString();
        let sMinute = minute.toString();
        let sSecond = second.toString();
        if (hour < 10) {
            sHour = "0" + hour;
        }
        if (minute < 10) {
            sMinute = "0" + minute; 
        }
        if (second < 10) {
            sSecond = "0" + second;    
        }

        return  [sHour,sMinute,sSecond]
    }
}

export default new Utils();
